.hero{
    display: flex;
    justify-content: space-between;
}
.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.right-h{
    flex: 1;
    position: relative;
    background-color: var(--orange);

}
.the-best-ad{
    margin: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    padding: 20px 13px;
    width: fit-content;
    text-transform: uppercase;
    color: white;
    align-items: center;
    display: flex;
    justify-content: flex-start;
    position: relative;
}
.the-best-ad>span{
    z-index: 2;
}
.the-best-ad>div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;
}
.hero-text{
    display: flex;
    text-transform: uppercase;
    gap: 1.5rem;
    font-weight: bold;
    font-size: 4.5rem;
    flex-direction: column;
    color: white;
    text-overflow: inherit;
}
.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    letter-spacing: 1px;
    text-transform: none;
    font-weight: 200;
    width: 80%;
}
.figures{
    display: flex;
    gap: 2rem;

}
.figures>div{
    display: flex;
    flex-direction: column;
}
.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}
.figures>div>span:nth-of-type(2){
    color: var(--gray);
    text-transform: uppercase;
}
.hero-buttons{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}
.hero-buttons>:nth-of-type(1){
    background-color: var(--orange);
    color: white;
    width: 8rem;
}
.hero-buttons>:nth-of-type(2){
    background-color: transparent;
    color: white;
    width: 8rem;
    border: 2px solid var(--orange);
}
.right-h>.btn{
    position: absolute;right: 3rem;top: 2rem;color: black;
}
.heart-rate{
    display: flex;
    padding: 1rem;
    gap: 1rem;
    align-items: start;
    flex-direction: column;
    background-color: var(--darkGrey);
    width: fit-content;
    border-radius: 5px;
    top: 7rem;
    right: 4rem;
    position: absolute;
}
.heart-rate :nth-child(2){
    color: var(--gray);
}
.heart-rate :nth-child(3){
    color: white;
    font-size: 1.5rem;
}
.heart-rate>img{
    width: 2rem;
}
.hero-image{
    position: absolute;
    top: 10rem;
    right: 8rem;
    width: 23rem;
}
.hero-image-back{
    position: absolute;
    right: 20rem;
    top: 4rem;
    z-index: -1;
    width: 15rem;
}
.calories{
    display: flex;
    background-color: var(--caloryCard);
    gap: 2rem;
    position: absolute;
    width: 14rem;
    border-radius: 5px;
    top: 35.5rem;
    right: 32rem;
    padding: 1rem;
}
.calories > img{
    width: 3rem;
}
.calories>div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.calSpan1{
    color: var(--gray);
}
.calSpan2{
    color: white;
    font-size: 1.5rem;
}
.hero-blur{
    width: 22rem;
    height: 30rem;
    left: 0;
}