.programs{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 2rem;
}
.programs-header{
    display: flex;
    gap: 5rem;
    justify-content: center;
    text-transform: uppercase;
    font-size: 3rem;
    font-weight: bold;
    color: white;
    font-style: italic;
}
.programs-categories{
    display: flex;
    gap: 1rem;
}
.category{
    display: flex;
    flex-direction: column;
    background-color: gray;
    color: white;
    gap: 1rem;
    padding: 2rem;
    justify-content: space-between;

}

.category>:nth-child(1){
    width: 2rem;
    height: 2rem;
    fill: white;
}
.category>:nth-child(2){
     font-size: 1rem;
     font-weight: bold;
}
.category>:nth-child(3){
     font-size: 0.9rem;
     line-height: 25px;
}
.join-now{
    display: flex;
    align-items: center;
    gap: 2rem;
}
.join-now>img{
    width: 1rem;
}
.category:hover{
    background: var(--planCard);
    cursor: pointer;
}
